}

.link-item:hover {
  color: #fff;
}

.legal {
  text-align: center;
  font-size: 14px;
  color: #888;
}

.ourwork {
  padding: 20px 0;
  background: linear-gradient(to bottom right, #f05b36, #393b98);
}

.ourwork-section {
  display: grid;
  padding: 40px 100px;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 40px;
}

.left-container {
  font-size: 15px;
}

.right-container {
}

.work-image {
  width: 100%;
  height: 400px;
  border-radius: 20px;
}

.highlight {
  background-color: #eeeeee;
}

.work-button {
  outline: 0;
  border: none;
  background-color: transparent;
}

.work-icon,
.work-icon-w {
  height: 40px;
  width: 40px;
  transition: all 0.3s;
}

.work-icon {
  fill: #eeeeee;
}

.work-icon-w {
  fill: #262626;
}

.work-icon:hover,
.work-icon-w:hover {
  fill: #393b98;
}

